import request from '@utils/request'

export const getGraphs = (params = {}) => {
  return request({
    url: 'graphs',
    method: 'get',
    params,
  })
}

export const createGraph = (data) => {
  return request({
    url: 'graphs',
    method: 'post',
    data,
  })
}

export const getGraphDetail = (id: string, params = {}) => {
  return request({
    url: `graphs/${id}`,
    method: 'get',
    params,
  })
}

export const updateGraph = (id: string, data) => {
  return request({
    url: `graphs/${id}`,
    method: 'put',
    data,
  })
}

export const deleteGraph = (id: string) => {
  return request({
    url: `graphs/${id}`,
    method: 'delete',
  })
}
