import React, { Component, Fragment } from 'react'
import { Table, Input, Button, Tooltip, Modal, Collapse } from 'antd'
import { Topology } from '@topology/core'

import type { IListMixin } from '@interfaces/mixin'
import style from './index.module.less'
import { Tools } from './tools'
import { getGraphDetail, updateGraph } from '@apis/graph'

const { Panel } = Collapse

export default class GraphDetailView extends Component<IListMixin, any> {
  private canvasRef = null
  private canvas = null

  async componentDidMount() {
    // @ts-ignore
    const { id } = this.props.match.params
    const res: any = await getGraphDetail(id)
    if (this.canvasRef) {
      this.canvas = new Topology(this.canvasRef, {})
      if(res && res.content){
        this.canvas.open(res.content);
      }
    }
  }

  getCanvas(ref) {
    this.canvasRef = ref
  }

  dragStart(event, node) {
    event.dataTransfer.setData('Topology', JSON.stringify(node.componentData || node.data))
  }

  async save() {
    // @ts-ignore
    const { id } = this.props.match.params

    const { data } = this.canvas
    const dd = await updateGraph(id, {
      content: data,
    })
  }

  render() {
    return (
      <div className={style['graph-wrap']}>
        <div className={style['graph-actions']}>
          <Button
            type="primary"
            size="small"
            className="m-r-8"
            onClick={() => {
              this.save()
            }}
          >
            保存
          </Button>
          <Button size="small">取消</Button>
        </div>
        <div className={style['graph-left']}>
          {Tools.map((n: any, index) => {
            return (
              <Collapse key={index} defaultActiveKey={[Tools[0].name]} ghost>
                <Panel header={n.name} key={n.name}>
                  {n.list.map((t) => {
                    return (
                      <div
                        key={t.name}
                        className={style['graph-icon']}
                        draggable={true}
                        onDragStart={($event) => {
                          this.dragStart($event, t)
                        }}
                      >
                        <i className={`iconfont ${t.icon}`}></i>
                      </div>
                    )
                  })}
                </Panel>
              </Collapse>
            )
          })}
        </div>
        <div
          id="topology-canvas"
          ref={(ref) => {
            this.getCanvas(ref)
          }}
          className={style['graph-center']}
        ></div>
        {/* <div className={style['graph-right']}>r</div> */}
      </div>
    )
  }
}
